
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

.nav_bg {
  width:96%;
  text-align: center;
  padding-left: 3%;
  padding-right: 1%;
  /*border-radius: 0em 0em 1em 1em;*/
  -webkit-box-shadow: 0.1em 0.3em 0.3em #072927;
          box-shadow: 0.1em 0.3em 0.3em #072927;
  margin-bottom: 0.3em;
  background: #f0ba02;
  /*background: linear-gradient(#f0ba02, #cd7015, #f0ba02);*/
}
.nav{
  width: 4em;
}
.nav_btn{
  float: right;
  margin-right: 8%;
  margin-top: 0.5em;
  font-size: 1.2em;
  color: white;
  font-weight: bold;
  background: transparent;
}
.nav_item{
  text-align: center;
  margin-top: 0.5em;
  padding: 5px;
  font-size: 1em;
  background: #cd7015;
  border-color: brown;
}
.left_radius{
  border-radius: 1em 0em 0em 1em;
}
.right_radius{
  border-radius: 0em 1em 1em 0em;
}
.nav_active{
  background: white;
  border-color: #cd7015;
  color: coral;
  font-weight:bold;
}
.Japanese{
  padding-bottom: 1%;
}
.container{
  width:100%;
  text-align: center;
  margin-bottom: 5%;
  /*border-spacing: inherit;*/
  background: gray;
  empty-cells: show;
}
.container tr {
  text-align: center;
  /*background-color: #21ff13;*/
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.normal_row{
  text-align: center;
  width:20%;
}
.less_row{
  width:33%;
  text-align: center;
}
.letter{
  margin: 0%;
  font-size: 1.4em;
  line-height: 1.5em;
  color: #787250;
  background: #F8F6EA;
  padding-top: 0.4em;
  padding-bottom: 0.2em;
}
.active{
  font-size: 2.0em;
  line-height: 0.87em;
  color: coral;
  font-weight:bold;
}
#name{
  margin: 0%;
  font-size: 1.4em;
  line-height: 1.5em;
  color: #928B64;
  background: #EAE7D1;
  padding-bottom: 1%;
  font-size: 1em;
}
.box {
  position: absolute;
  width: 80px;
  height: 50px;
  background-color: #09f;
